import React from 'react'
import {
    BrowserRouter as Router,
    Switch,
    Route
} from 'react-router-dom';
import { Navbar } from '../components/ui/Navbar';

import { FlickrApp } from '../FlickrApp';
import { AppFeeds } from '../AppFeeds';


export const AppRouter = () => {
    return (
        <Router>
            <div>
                <Navbar />


                <Switch>
                    <Route exact path="/feeds" component={AppFeeds} />
                    <Route exact path="/" component={FlickrApp} />

                </Switch>
            </div>
        </Router>
    )
}
