import React from 'react';
import { useSearchPhoto } from '../hooks/SarchPhotos';
import { PhotoGItem } from './PhotosGridItem';

export const PhotoGrid = ({ category }) => {

    const { data: images, loading } = useSearchPhoto(category);

    return (
        <>
            <h3 className="animate__animated animate__fadeIn"> {category} </h3>

            {loading && <p className="animate__animated animate__flash">Loading</p>}

            <div className="card-grid">

                {
                    images.map(img => (
                        <PhotoGItem
                            key={img.id}
                            {...img}
                        />
                    ))
                }

            </div>
        </>
    )
}
