import React, { useState } from 'react'
import { GridFeeds } from './components/GridFeeds'

export const AppFeeds = () => {

    const [categories, setCategories] = useState(['']);
    return (
        <>
            <h2>Feeds Public</h2>

            <hr />

            <ol>
                {
                    categories.map(category => (
                        <GridFeeds
                            key={category}
                            category={category}
                        />
                    ))
                }
            </ol>

        </>
    )
}
