import React from 'react';
import { useSearchFeeds } from '../hooks/SearchFeeds';
import { GItemFeeds } from './GridItemFeeds';

export const GridFeeds = () => {

    const { data: images, loading } = useSearchFeeds();

    return (
        <>
            {loading && <p className="animate__animated animate__flash">Loading</p>}

            <div className="card-grid">

                {
                    images.map(img => (
                        <GItemFeeds
                            key={img.id}
                            {...img}
                        />
                    ))
                }

            </div>
        </>
    )
}
