import { useState, useEffect } from 'react'
import { getPhoto } from '../helpers/getPhotos';


export const useSearchPhoto = (category) => {

    const [state, setState] = useState({
        data: [],
        loading: true
    });

    useEffect(() => {

        getPhoto(category)
            .then(imgs => {

                setState({
                    data: imgs,
                    loading: false
                });
            })

    }, [category])




    return state;


}


