


export const getFeeds = async () => {



    const url = `https://cors-anywhere.herokuapp.com/https://www.flickr.com/services/feeds/photos_public.gne?format=json&nojsoncallback=1`;
    console.log(url);
    const resp = await fetch(url);

    const resp_ = await resp.json();

    const photos = resp_.items.map(itms => {
        let titleL = itms.title;
        if (titleL.length > 10) {
            titleL = titleL.substring(0, 10) + '...';
        }
        return {
            id: itms.author_id,
            title: titleL,
            url: itms.media.m,
            urldetalle: itms.link
        }
    })

    return photos;


}