import React, { useState } from 'react'
import { AddText } from './components/AddText';
import { PhotoGrid } from './components/PhotosGrid';

export const FlickrApp = () => {

    const [categories, setCategories] = useState(['Technology']);

    return (
        <>
            <h2>Search Photo</h2>
            <AddText setCategories={setCategories} />
            <hr />

            <ol>
                {
                    categories.map(category => (
                        <PhotoGrid
                            key={category}
                            category={category}
                        />
                    ))
                }
            </ol>

        </>
    )
}
