


export const getPhoto = async (category) => {


    const url = `https://www.flickr.com/services/rest/?method=flickr.photos.search&api_key=a2f7e30f8ad056183771b4ca3f0f2376&text=${encodeURI(category)}&privacy_filter=1&format=json&nojsoncallback=1`;


    console.log(url);
    const resp = await fetch(url);

    const resp_ = await resp.json();
    console.log(resp_);
    const photos = resp_.photos.photo.map(img => {
        let titleL = img.title;
        if (titleL.length > 10) {
            titleL = titleL.substring(0, 10) + '...';
        }

        return {
            id: img.id,
            title: titleL,
            url: `https://farm${img.farm}.staticflickr.com/${img.server}/${img.id}_${img.secret}.jpg`,
            urldetalle: `https://www.flickr.com/photos/${img.owner}/${img.id}`
        }
    })

    return photos;


}