import { useState, useEffect } from 'react'
import { getFeeds } from '../helpers/getFeeds';


export const useSearchFeeds = () => {

    const [state, setState] = useState({
        data: [],
        loading: true
    });

    useEffect(() => {

        getFeeds()
            .then(imgs => {

                setState({
                    data: imgs,
                    loading: false
                });
            })

    })




    return state;


}
